import { observable, action, computed } from 'mobx';
import * as api from '../lib/api';
import cheerio from 'cheerio';

export default class goldStore {
  @observable date = new Date();
  @observable week = []; //년-월-일
  @observable weekNotYear = []; //월-일
  @observable.ref buySise = []; //살때 시세 - 데이터 뿌리는 값
  @observable sellSise = []; //팔때 시세 - 데이터 뿌리는 값
  @observable calculatorToggle = false; //그람 계산기 토글
  @observable buyGoldValue = null; //그람 계산기 인풋 벨류
  @observable todayBuySise = null; //오늘 순금 살때 가격 - 그람 계산기로 계산 시 사용
  @observable todaySellSise = null; //오늘 순금 팔때 가격 - 그람 계산기로 계산 시 사용

  constructor(root) {
    this.root = root;

    this.actGoldList();
  }

  @action
  actGoldList = async weekValue => {
    try {
      this.root.common.loading = true;

      const response = await api.goldSise();
      console.log(response.data);

      this.root.common.loading = false;
    } catch (e) {
      console.log(e);
    }
  };

  @action
  actGoldToggleTrue = () => {
    this.calculatorToggle = true;
  };
  @action
  actGoldToggleFalse = () => {
    this.calculatorToggle = false;
  };
  @action
  actGoldValue = value => {
    value ? (this.buyGoldValue = parseInt(value)) : (this.buyGoldValue = 0);
  };

  @computed
  get buyTotal() {
    return (this.buyGoldValue * this.todayBuySise) / 3.75;
  }
  @computed
  get sellTotal() {
    return (this.buyGoldValue * this.todaySellSise) / 3.75;
  }
}

